.home-collage {
  background: url("../images/home-collage-bg.png") no-repeat 50% 0;

  .container {
    @include flexbox();
    @include align-items(center);
    min-height: 600px;
  }

  .home-collage-content {
    width: 100%;
    color: $color-white;
  }

  h1 {
    font-weight: 700;
    font-size: 112px;
    line-height: 0.91;
    letter-spacing: normal;
    text-align: left;
    max-width: 660px;

    @media (max-width: $grid-float-breakpoint) {
      font-size: 78px;
    }

  }

  p {
    font-size: 26px;
    line-height: 1.08;
    font-weight: 700;
    margin: 30px 0 0 0;

    @media (max-width: $grid-float-breakpoint) {
      br {
        display: none;
      }
    }

  }
}

.home-section {
  padding: 70px 0;

  @media (max-width: $grid-float-breakpoint) {
    padding: 35px 0;
  }

  .section-title {
    color: $color-jetblack;
    font-size: 64px;
    font-weight: 700;
    margin: 0 0 53px 0;

    @media (max-width: $grid-float-breakpoint) {
      margin: 0;
    }

  }

  .section-link {
    padding: 25px 0 0 0;

    a {
      color: $color-active;
      font-size: 18px;
      font-weight: 700;
      letter-spacing: 3.6px;
      text-decoration: none;
      text-transform: uppercase;
      border-bottom: 2px solid $color-active;
      display: inline-block;
      padding: 0 0 6px 0;

      &:hover {
        border-bottom-color: transparent;
      }
    }
  }

}

.home-slogan {
  background: #b9e5e3 url("/assets/images/pattern_bg_color_light.png") repeat 0 0;

  .home-slogan-text {
    padding-top: 18px;
  }

  h2 {
    text-transform: uppercase;
    margin: 0 0 13px 0;
    padding: 0;
    color: #666;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 3.6px;
  }

  .highlighted-text {
    font-weight: 700;
    font-size: 50px;
    line-height: 1.57;
    color: $color-teal;

    @media (max-width: $grid-float-breakpoint) {
      font-size: 33.8px;
    }

    span {
      background: $color-white;
      padding: 2px 10px;
    }
  }

  .section-link {
    padding-top: 9px;
  }
}

.faces-section {
  background: $color-ice-blue;

  .team-member {
    position: relative;
    border-radius: 3px;
    max-width: 270px;
    margin-left: auto;
    margin-right: auto;

    @media (max-width: $grid-float-breakpoint) {
      margin: 15px auto;
    }

    &:after {
      width: 100%;
      background: rgba(50, 120, 120, 0.4);
      content: '';
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 1;
      border-radius: 3px;
    }

    img {
      max-width: 100%;
    }

    .team-member-name {
      z-index: 2;
      position: absolute;
      bottom: 20px;
      left: 20px;
      color: $color-white;
      font-weight: 700;
      letter-spacing: 2.6px;
      line-height: 1.2;
      font-size: 26px;
    }

    .team-member-position {
      font-size: 18px;
      letter-spacing: 3.6px;
      text-transform: uppercase;
    }

    .team-member-description {
      font-size: 22px;
      font-weight: 500;
      line-height: 1.09;
      color: $color-white;
      z-index: 2;
      position: absolute;
      bottom: 20px;
      left: 20px;
    }

    &:hover {
      .team-member-name {
        display: none;
      }
      .team-member-description {
        display: block;
      }

      &:after {
        display: none;
      }

    }

    .team-member-description {
      display: none;
    }
  }
}

.clients-section {
  .client-title {
    color: #e1b84b;
    font-size: 46px;
    line-height: 1;
    font-weight: 700;
    border-bottom: 2px solid #d1d3d4;
    padding: 0 0 13px 0;
    margin: 0 0 19px 0;
  }

  .client-image {
    min-height: 41px;
    vertical-align: bottom;
    margin: 0 0 8px 0;

    img {
      vertical-align: bottom;
    }
  }

  .client-description {
    color: $color-jetblack;
    font-size: 22px;
    line-height: 1.09;
    max-width: 295px;
  }

  .past-clients-list {
    padding: 20px 0 0 0;
    @include flexbox();
    @include justify-content(space-between);

    @media (max-width: $grid-float-breakpoint) {
      @include flex-wrap(wrap);
    }
  }

  @media (max-width: $grid-float-breakpoint) {
    .client-item {
      margin: 0 0 90px 0;
    }
  }

}

.section-footer {
  margin: 70px 0 0 0;

  @media (max-width: $grid-float-breakpoint) {
    margin-top: 35px;
  }

  .section-footer-link {
    display: block;
    border-bottom: 2px solid $color-active;
    padding: 0 0 9px 0;
    font-size: 18px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 3.6px;
    color: $color-active;
    text-transform: uppercase;

    &:hover {
      text-decoration: none;
    }
  }
}

.knowledge-section {
  background: $color-ice-blue;

  .knowledge-subtitle {
    text-transform: uppercase;
    color: #666;
    font-size: 18px;
    line-height: 1.47;
    letter-spacing: 3.6px;
    border-bottom: 2px solid #aaa;
    padding: 0 0 5px 0;
    font-weight: 700;
  }

  .knowledge-item {

    @media (max-width: $grid-float-breakpoint) {
      margin-bottom: 60px;
    }

    h2 {
      font-weight: 700;
      font-size: 46px;
      margin: 12px 0 25px 0;
    }

    ul {
      list-style: none;
      margin: 20px 0;

      li {
        padding: 2px 0;
      }

      a {
        text-transform: uppercase;
        letter-spacing: 1.1px;
        font-size: 18px;
        line-height: 1.33;
        font-weight: 700;
      }
    }
  }

  .ecommerce-knowledge {
    h2, a {
      color: $color-ecommerce;
    }
  }

  .biotech-knowledge {
    h2, a {
      color: $color-biotech;
    }
  }

  .energy-knowledge {
    h2, a {
      color: $color-energy;
    }
  }

}

.partners-section {
  .partners-title {
    text-transform: uppercase;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 3.6px;
    border-bottom: 2px solid #999;
    margin: 0 0 28px 0;
    padding: 0 0 5px 0;
  }

  .partners-list {
    @include flexbox();
    @include justify-content(space-between);
    @include align-items(center);

    @media (max-width: $grid-float-breakpoint) {
      display: block;
    }

    img {
      height: 10%;

      @media (max-width: $grid-float-breakpoint) {
        display: block;
        margin: 25px 0;
      }

    }
  }
}

.blog-section {
  background: $color-ice-blue;

  @media (max-width: $grid-float-breakpoint) {
    .blog-post-item {
      margin: 35px 0 0 0;
    }
  }

  .blog-post-thumbnail {
    display: block;
    width: 100%;
    height: 240px;
    overflow: hidden;
    background-size: cover;
    background-origin: border-box;
    background-position: 50% 50%;
    box-shadow: 0px 3px 12px 0 rgba(0, 0, 0, 0.1);
    position: relative;
    margin: 0 auto 6px auto;
    border-radius: 3px;

    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      background: rgba(255, 255, 255, 0.4);
    }

    &:hover {
      &:after {
        background: transparent;
      }
    }

    img {
      opacity: 0;
    }
  }

  .blog-post-name {
    font-size: 26px;
    color: $color-jetblack;
    font-weight: 700;
    line-height: 1;

    .blog-post-tags {
      display: block;
      margin: 3px 0 0 0;
      color: #889ea6;
      font-size: 18px;
      text-transform: uppercase;
      letter-spacing: 1.1px;
    }

    &:hover {
      text-decoration: none;
      color: $color-active;

      .blog-post-tags {
        color: $color-active;
      }
    }
  }
}

.footer-contact-section {
  background: linear-gradient(to right, $color-flamingo 50%, $color-jetblack 50%);
  padding: 0;

  @media (max-width: $grid-float-breakpoint) {
    background: linear-gradient(to bottom, $color-flamingo 50%, $color-jetblack 50%);
  }

  .footer-contact-section-inner {
    background: url("/assets/images/pattern.png") repeat 0 0;
    padding: 84px 0 50px 0;

    @media (max-width: $grid-float-breakpoint) {
      padding-top: 40px;
    }
  }

  .footer-contact-form {
    max-width: 500px;

    .footer-contact-form-title {
      font-size: 64px;
      font-weight: 700;
      line-height: 1.03;
      letter-spacing: normal;
      color: $color-white;
      margin: 0 0 65px 0;

      @media (max-width: $grid-float-breakpoint) {
        margin-bottom: 35px;
      }
    }

    .form-control {
      background: rgba(255, 255, 255, 0.25);
      border: 0;
      border-radius: 5px;
      font-size: 26px;
      letter-spacing: 1.3px;
      font-weight: 700;
      color: $color-white;
      @include placeholder(#fff);
      padding: 26px 20px 30px 20px;
      margin: 0 0 40px 0;

      @media (max-width: $grid-float-breakpoint) {
        margin: 0 0 20px 0;
      }

      &:focus {
        outline: 0;
        border: 0;
        box-shadow: none;
      }
    }

    textarea.form-control {
      max-height: 220px;
      padding: 11px 20px;
    }

  }

  .btn-blue {
    background: $color-darkblue;
    color: $color-white;
    text-transform: uppercase;
    font-size: 18px;
    letter-spacing: 3.6px;
    padding: 16px 50px;
  }

  .footer-text-contacts {
    padding: 130px 0 0 35px;

    dt {
      color: $color-pale-salmon;
      font-size: 16.8px;
      font-weight: 700;
      text-transform: uppercase;
      line-height: 1.22;
      letter-spacing: 3.4px;
      display: block;
    }

    dd {
      display: block;
      color: $color-white;
      margin: 12px 0 30px 0;
      font-size: 21px;
      font-weight: 500;
      line-height: 1.22;

      .visit-subtitle {
        font-size: 16.8px;
      }
    }
  }
}