html {
  position: relative;
  min-height: 100%;
}

body {
  font-weight: 500;
  margin-bottom: 312px;

  @media (max-width: $grid-float-breakpoint) {
    margin-bottom: 0;
  }
}

#main-content {
  margin-top: $navbar-height;
}

.site-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 312px;
  background-color: $color-pale-turquoise;

  @media (max-width: $grid-float-breakpoint) {
    position: relative;
    height: auto;
  }

}

img {
  max-width: 100%;
}