@font-face {
    font-family: 'Acre';
    src: url('../fonts/acre/acre-medium-webfont.eot');
    src: url('../fonts/acre/acre-medium-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/acre/acre-medium-webfont.woff2') format('woff2'),
         url('../fonts/acre/acre-medium-webfont.woff') format('woff'),
         url('../fonts/acre/acre-medium-webfont.ttf') format('truetype'),
         url('../fonts/acre/acre-medium-webfont.svg#webfontregular') format('svg');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'Acre';
    src: url('../fonts/acre/acre-bold-webfont.eot');
    src: url('../fonts/acre/acre-bold-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/acre/acre-bold-webfont.woff2') format('woff2'),
         url('../fonts/acre/acre-bold-webfont.woff') format('woff'),
         url('../fonts/acre/acre-bold-webfont.ttf') format('truetype'),
         url('../fonts/acre/acre-bold-webfont.svg#webfontregular') format('svg');
    font-weight: 700;
    font-style: normal;

}