.site-header {
  background: $color-teal;

  .nav {

    @media (max-width: $grid-float-breakpoint) {
      @include flexbox();
    }

    a {
      color: $color-white;
      font-weight: 700;
      text-transform: uppercase;
      font-size: 18px;
      line-height: 1.2;
      letter-spacing: 3.6px;

      &:hover, &:focus {
        color: #73ffc9;
      }

      @media (max-width: $grid-float-breakpoint) {
        font-size: 14px;
        padding-left: 8px;
        padding-right: 8px;
      }

      span {
        border-bottom: 2px solid transparent;
        display: block;
        padding: 0 1px 2px 3.6px;
      }

      &.active {
        color: #73ffc9;

        span {
          border-color: #73ffc9;
        }
      }
    }

    .nav-lang {
      margin-left: 35px;

      @media (max-width: $grid-float-breakpoint) {
        display: none;
      }

      & + .nav-lang {
        margin-left: 0;
      }

      a {
        padding-left: 5px;
        padding-right: 5px;
      }
    }

    > li {
      float: left;

      @media (max-width: $grid-float-breakpoint) {
        text-align: center;

        a {
          display: inline-block;
        }
      }

    }

  }
}

@media (max-width: $grid-float-breakpoint) {
  .navbar-nav {
    margin-left: 0;
    margin-right: 0;
    clear: both;
  }
}

.mobile-lang {
  display: none;

  @media (max-width: $grid-float-breakpoint) {
    display: block;
    float: right;
    margin: 40px 0 0 0;

    li {
      list-style: none;
      display: inline-block;

      a {
        color: $color-white;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 18px;
        line-height: 1.2;
        letter-spacing: 3.6px;

        &.active {
          color: #73ffc9;
        }
      }
    }
  }

}