.site-footer {
  padding: 91px 0 0 0;

  @media (max-width: $grid-float-breakpoint) {
    padding-top: 35px;
  }

  .footer-menus {
    overflow: hidden;

    .menu-col {
      float: left;
      margin: 0 30px 0 0;

      @media (max-width: $grid-float-breakpoint) {
        float: none;
      }

      .menu-col-title {
        font-size: 14px;
        font-weight: 700;
        line-height: 1.2;
        color: $color-jetblack;
        text-transform: uppercase;
        margin: 0 0 2px 0;
      }

      ul {
        list-style: none;

        li {
          padding: 0 0 2px 0;
        }

        a {
          text-transform: uppercase;
          font-weight: 700;
          color: $color-teal;
          font-size: 14px;
        }
      }
    }
  }

  .footer-copyright {
    margin: 20px 0 0 0;
    font-size: 14px;
    color: $color-jetblack;
    letter-spacing: 0.1px;
  }

  .footer-right {
    text-align: right;

    @media (max-width: $grid-float-breakpoint) {
      text-align: left;
      margin-top: 35px;
    }

  }
}